import React, { useState } from "react";
import { Link } from 'react-router-dom';
import "./Header.css";

function Header() {
  const [click, setClick] = useState(false);
  const clickToggle = () => setClick(!click);

  const [isOpen, setMenu] = useState(false);
  const toggleMenu = () => {
    setMenu((isOpen) => !isOpen);
  };

  return (
    <>
      <nav className="nav_bar">
        <div className="nav_wrap">
          <div className="nav_top">
            <Link to="/" exact className="nav_logo">
              <h1>헬시버니</h1>
              <img src="./img/logo.png" alt="메인로고" />
            </Link>
            <div className="nav_icon" onClick={clickToggle}>
              <i
                className={click ? "fas fa-times" : "fas fa-bars"}
                onClick={() => toggleMenu()}
              ></i>
            </div>
          </div>

          <div className={isOpen ? "nav_menu_on" : "nav_menu"}>
            <div className="setting_bar">
              <Link to="/Login">로그인을 해주세요</Link>
                <p><img src="./img/ham_icon/setting.png" alt="설정" /></p>
            </div>

            <ul className="nav_list">
              <li className="nav_item">
                <Link to="/" className="nav_links">
                  <img src="./img/ham_icon/coupon.png" alt="쿠폰" />
                  <span>쿠폰</span>
                </Link>
              </li>
              <li className="nav_item">
                <Link to="/" className="nav_links">
                  <img src="./img/ham_icon/my.png" alt="마이" />
                  <span>마이페이지</span>
                </Link>
              </li>
              <li className="nav_item">
                <Link to="/" className="nav_links">
                  <img src="./img/ham_icon/point.png" alt="포인트" />
                  <span>포인트</span>
                </Link>
              </li>
              <li className="nav_item">
                <Link to="/" className="nav_links">
                  <img src="./img/ham_icon/counsel.png" alt="상담" />
                  <span>상담신청</span>
                </Link>
              </li>
            </ul>

            <div className="cate_wrap">
              <h3>카테고리</h3>
              <div className="categorie">
                <ul>
                  <li>
                    <Link to="/">
                      <img src="./img/ctgr01.png" alt="100" />
                      <span>100원특가</span>
                    </Link>
                  </li>
                  <li>
                    <Link to="/">
                      <img src="./img/ctgr02.png" alt="new" />
                      <span>신제품</span>
                    </Link>
                  </li>
                  <li>
                    <Link to="/">
                      <img src="./img/ctgr03.png" alt="summer" />
                      <span>여름특가</span>
                    </Link>
                  </li>
                  <li>
                    <Link to="/">
                      <img src="./img/ctgr04.png" alt="100" />
                      <span>볶음밥/도시락</span>
                    </Link>
                  </li>
                </ul>
                <ul>
                  <li>
                    <Link to="/">
                      <img src="./img/ctgr05.png" alt="instant" />
                      <span>즉석 간편식</span>
                    </Link>
                  </li>
                  <li>
                    <Link to="/">
                      <img src="./img/ctgr06.png" alt="salad" />
                      <span>샐러드/과일</span>
                    </Link>
                  </li>
                  <li>
                    <Link to="/">
                      <img src="./img/ctgr07.png" alt="vegan" />
                      <span>비건식품</span>
                    </Link>
                  </li>
                  <li>
                    <Link to="/">
                      <img src="./img/ctgr08.png" alt="bakery" />
                      <span>베이커리</span>
                    </Link>
                  </li>
                </ul>
                <ul>
                  <li>
                    <Link to="/">
                      <img src="./img/ctgr09.png" alt="chicken" />
                      <span>닭가슴살</span>
                    </Link>
                  </li>
                  <li>
                    <Link to="/">
                      <img src="./img/ctgr10.png" alt="chicken2" />
                      <span>닭안심살</span>
                    </Link>
                  </li>
                  <li>
                    <Link to="/">
                      <img src="./img/ctgr11.png" alt="snacks" />
                      <span>과자/간식</span>
                    </Link>
                  </li>
                  <li>
                    <Link to="/">
                      <img src="./img/ctgr12.png" alt="pack" />
                      <span>식단패키지</span>
                    </Link>
                  </li>
                </ul>
              </div>
            </div>

            <div className="search">
            <input type="text" placeholder="찾고 있는 상품이 있나요?" />
            <button>
              <img src="./img/search.png" alt="검색" />
            </button>
          </div>
          </div>
        </div>
      </nav>
    </>
  );
}

export default Header;
