/* 헬시툰 페이지 */
import '../App.css';
import 'bootstrap/dist/css/bootstrap.min.css';

import Common from "../components/Common";
import styled from 'styled-components'
import { Swiper, SwiperSlide } from 'swiper/react';
import Carousel from 'react-bootstrap/Carousel';

import 'swiper/css';
import 'swiper/css/navigation';
import 'swiper/css/pagination';
import 'swiper/css/scrollbar';

const Sub03Box = styled.div`
width: 100vw;

  .toonSlide{
    width: 100vw;
    height: 63vh;
    background-color: white;

    .carousel {
      height:100%
    }
    img{
      width:100vw;
    }
  }

  .toon{
    height: 40vh !important;

         h2{
         font-size:1.3rem;
        }
  }

  .Swiper{
    height: 50vh !important;
    .swiper-slide{
      height: 50vh !important;
    }
  }


`


function Sub03() {
  return (
    <Sub03Box>
      <Common />
      {/*  */}
      <div className='toonSlide'>
        <Carousel>
          <Carousel.Item>
            <img src={process.env.PUBLIC_URL + "/img/toon_banner1.png"} />
          </Carousel.Item>
          <Carousel.Item>
            <img src={process.env.PUBLIC_URL + "/img/toon_banner2.png"} />
          </Carousel.Item>

        </Carousel>

      </div>
      <div className='tooncont1 toon'>
        <h2>헬시버니 TOON</h2>
        <Swiper
          spaceBetween={30}
          slidesPerView={2.5}
          onSlideChange={() => console.log('slide change')}
          onSwiper={(swiper) => console.log(swiper)}
        >
          <SwiperSlide>
            <div><img src={process.env.PUBLIC_URL + "/img/toon1.png"} /></div>
          </SwiperSlide>
          <SwiperSlide>
            <div><img src={process.env.PUBLIC_URL + "/img/toon2.png"} /></div>
          </SwiperSlide>
          <SwiperSlide>
            <div><img src={process.env.PUBLIC_URL + "/img/toon3.png"} /></div>
          </SwiperSlide>
          <SwiperSlide>
            <div><img src={process.env.PUBLIC_URL + "/img/toon1.png"} /></div>
          </SwiperSlide>
          <SwiperSlide>
            <div><img src={process.env.PUBLIC_URL + "/img/toon2.png"} /></div>
          </SwiperSlide>
          <SwiperSlide>
            <div><img src={process.env.PUBLIC_URL + "/img/toon3.png"} /></div>
          </SwiperSlide>
        </Swiper>
      </div>

      <div className='tooncont2 toon'>
        <h2>에디터가 선정한 다이어트 꿀팁</h2>
        <Swiper
          spaceBetween={30}
          slidesPerView={2.5}
          onSlideChange={() => console.log('slide change')}
          onSwiper={(swiper) => console.log(swiper)}
        >
          <SwiperSlide>
            <div><img src={process.env.PUBLIC_URL + "/img/ed1.png"} /></div>
          </SwiperSlide>
          <SwiperSlide>
            <div><img src={process.env.PUBLIC_URL + "/img/ed2.png"} /></div>
          </SwiperSlide>
          <SwiperSlide>
            <div><img src={process.env.PUBLIC_URL + "/img/ed3.png"} /></div>
          </SwiperSlide>
          <SwiperSlide>
            <div><img src={process.env.PUBLIC_URL + "/img/ed1.png"} /></div>
          </SwiperSlide>
          <SwiperSlide>
            <div><img src={process.env.PUBLIC_URL + "/img/ed2.png"} /></div>
          </SwiperSlide>
          <SwiperSlide>
            <div><img src={process.env.PUBLIC_URL + "/img/ed3.png"} /></div>
          </SwiperSlide>
        </Swiper>
      </div>

      <div className='tooncont3 toon'>
        <h2>헬시버니 연구소</h2>
        <Swiper
          spaceBetween={30}
          slidesPerView={2.5}
          onSlideChange={() => console.log('slide change')}
          onSwiper={(swiper) => console.log(swiper)}
        >
          <SwiperSlide>
            <div><img src={process.env.PUBLIC_URL + "/img/y1.png"} /></div>
          </SwiperSlide>
          <SwiperSlide>
            <div><img src={process.env.PUBLIC_URL + "/img/y2.png"} /></div>
          </SwiperSlide>
          <SwiperSlide>
            <div><img src={process.env.PUBLIC_URL + "/img/y3.png"} /></div>
          </SwiperSlide>
          <SwiperSlide>
            <div><img src={process.env.PUBLIC_URL + "/img/y1.png"} /></div>
          </SwiperSlide>
          <SwiperSlide>
            <div><img src={process.env.PUBLIC_URL + "/img/y2.png"} /></div>
          </SwiperSlide>
          <SwiperSlide>
            <div><img src={process.env.PUBLIC_URL + "/img/y3.png"} /></div>
          </SwiperSlide>
        </Swiper>
      </div>


    </Sub03Box>


  );


}

export default Sub03;
