import './App.css';
import React, { useEffect, useState } from 'react';
import {  Routes, Route, Link } from 'react-router-dom'
import 'bootstrap/dist/css/bootstrap.min.css';

/* 라우트 */
import Main from './routes/Main';
import Sub01 from './routes/Sub01';
import Sub02 from './routes/Sub02';
import Sub03 from './routes/Sub03';
import Login from './routes/Login';
function App() {

  let [intro, setIntro] = useState(true);

  useEffect(() => {
    let time = setTimeout(() => {
      setIntro(false);
    }, 2500);
    return () => {
      clearTimeout(time);
    }
  });

  return (
    <>
      <Routes>
        <Route
          path="/"
          element={
            <>
              <div>
                {
                  intro === true ? (
                    <div className='introWrap'>
                    </div>
                  ) : null
                }
              </div>
              <div className='select'>
                <div className='counseling'>
                  <button><Link to="/Login">상담받기</Link></button>
                </div>
                <div className='shopping'>
                  <button><Link to="/Main">쇼핑하기</Link></button>
                </div>
              </div>
            </>
          }
        >
        </Route>
        <Route path='/Main' element={<Main />} />
        <Route path='/Sub01' element={<Sub01 />} />
        <Route path='/Sub02' element={<Sub02 />} />
        <Route path='/Sub03' element={<Sub03 />} />
        <Route path='/Login' element={<Login />} />
      </Routes>
    </>
  );
}

export default App;
