import '../App.css';
import React from 'react';
import Accordion from 'react-bootstrap/Accordion';

import { styled } from 'styled-components/';

const FooterBlock = styled.div`
    width: 100vw;
    height: 600px;
    background-color: #efefef;
    position:relative;
    margin-bottom: 10vh;

    .f_top{
        background-color: #f08825;
        width:100%;
        height:50px;

        ul{
            width:100vw;
            height:50px;
            display:grid;
            grid-template-columns: repeat(3, 20% 60% 20% );
            align-items: center;

            li{
                font-size:0.8rem;
                text-align:center;
            }
        }

    }

    .foot_menu{
        width:100vw;
        height:80px;
        background-color:white;

        ul{
            width:100vw;
            height:80px;
            display:grid;
            grid-template-columns: repeat(2, 50% );
            grid-template-rows: repeat(2, 50% );
            align-items: center;

            li{
                text-align:center;
                color: #999999;
                strong{
                    color:black;
                }
            }
        }
    }

    .sns_list{
        width:100vw;
        height: 50px;
        background-color:white;

        ul{
            width: 100vw;
            height: 50px;
            display:grid;
            grid-template-columns: repeat(4, 20%);
            align-items: center;
            justify-items: center;
            justify-content: space-around;   

            li{
                float:left;
                width: 40px;
                height: 40px;
            }

        }
    }

    .callcenter{
        width:100vw;
        height:150px;
        background-color:white;
        padding-top: 10px;

        h3{
            width:100vw;
            text-align:center;
            color: #f08825;
            font-size: 1.5rem;
            font-weight:bold;
            margin:0;

        }
        span{
            font-size: 1.5rem;
            text-align:center;
            font-weight:bold;
            line-height:26px;
            margin-bottom:10px;
        }
        p{
            height:18px;
            line-height:18px;
            margin:0;
            font-size:0.8rem;
            text-align:center;
        }
    }

    .en_info{
        width:100vw;
        height: auto;
        .accordion-header button{
            width:100vw;
            text-align:center;
            padding-left:100px
        }
    }


    .copy{
        width:100vw;
        height:30px;
        background-color:black;

        span{
            width:100vw;
            text-align:center;
            font-size:0.8rem;
            color:white;
            line-height:30px;
        }
    }

`

const Footer = () => {
    return (
        <FooterBlock>
            <div className='footer'>
                <div className='f_top'>
                    <ul>
                        <li>공지사항</li>
                        <li>[공지사항] 반품/교환...</li>
                        <li>+ 더보기</li>
                    </ul>
                </div>
                <div className='foot_menu'>
                    <ul>
                        <li>회사소개</li>
                        <li>이용약관</li>
                        <li><strong>개인정보처리방침</strong></li>
                        <li>멤버심 프로그램</li>
                    </ul>
                </div>
                <div className='sns_list'>
                    <ul>
                        <li className='insta'><span className='hidden'>인스타</span></li>
                        <li className='youtube'><span className='hidden'>유튜브</span></li>
                        <li className='blog'><span className='hidden'>블로그</span></li>
                        <li className='facebook'><span className='hidden'>페이스북</span></li>
                    </ul>
                </div>
                <div className='callcenter'>
                    <h3>고객센터</h3>
                    <span>1800-1004</span>
                    <p>월~금 10:00 ~ 17:00</p>
                    <p>점심시간 12:00 ~ 13:00</p>
                    <p>토/일/공유일 휴무</p>
                </div>
                <div className='en_info'>
                    <Accordion>
                        <Accordion.Item eventKey="0">
                            <Accordion.Header>사업자정보</Accordion.Header>
                            <Accordion.Body>
                                Lorem ipsum dolor sit amet, consectetur adipiscing elit, sed do
                                eiusmod tempor incididunt ut labore et dolore magna aliqua.
                            </Accordion.Body>
                        </Accordion.Item>
                    </Accordion>
                </div>
                <div className='copy'>
                    <span>Copyright &copy; HEALTHBUNNY ALL Right Reserved.</span>
                </div>
            </div>
        </FooterBlock>
    )
}

export default Footer;