import React from "react";
import './Indicator.css'
import {  Routes, Route, Link } from 'react-router-dom'



function Indicator() {
  return (
    <>
      <div className="navigator">
        <ul>
          <li className="list">
            <Link to="/Sub03">
            <img src="./img/indi/toon.png" alt="버니툰" />
            <span>버니툰</span>
            </Link>
          </li>
          <li className="list">
            <Link to="/Sub01">
            <img src="./img/indi/counsel.png" alt="상담" />
            <span>상담하기</span>
            </Link>
          </li>
          <li className="list active">
            <Link to="/">
            <img src="./img/indi/home.png" alt="홈" />
            <span>Home</span>
            </Link>
          </li>
          <li className="list">
            <Link to="/Sub02">
            <img src="./img/indi/recent.png" alt="최근" />
            <span>상품목록</span>
            </Link>
          </li>
          <li className="list">
            <Link to="/Login">
            <img src="./img/indi/mypage.png" alt="마이" />
            <span>로그인</span>
            </Link>
          </li>
        </ul>
      </div>
    </>
  );
}

export default Indicator;
